import React from 'react';
import { format, parseISO } from 'date-fns';

const SessionInfo = ({ startTime, summary }) => {
  if (!startTime && !summary) return null;

  const formatDate = (dateString) => {
    try {
      // dateString"yyyy-MM-dd HH:mm"
      const date = parseISO(dateString);
      return format(date, 'yyyyMMdd HH:mm');
    } catch (error) {
      console.error('Error formatting date:', error);
      return '';
    }
  };

  const formattedDate = startTime ? formatDate(startTime) : '';

  return (
    <div className="session-info">
      <h3></h3>
      <p>: {formattedDate}</p>
      {summary && (
        <>
          <p>: {summary['Total Distance (GPS)']}</p>
          <p>: {summary['Total Elapsed Time']}</p>
          <p>: {summary['Avg Speed (GPS)']}</p>
        </>
      )}
    </div>
  );
};

export default SessionInfo;